.block {
    background-color: brown;
    box-shadow: 0 0 0 4px black;
    margin: auto;
}

.emptyblock {
    box-shadow: 0 0 0 0px black;
}

.container {
    margin-top: 5vh;
}



.row {
    margin: 0 !important;
    padding-bottom: 0.13vh !important;
}

.tcontainer {
    
    border-bottom: 1vh solid white;
}

p {
    font-size: 1.5vw;
    margin: 0;
}

.resp-head {
    font-size: 2.5vw;
    margin: 0;
    padding-bottom: 2vh;
    
}



body {
    background-color: black;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.grid-item {
    height: 100%;
}

.btn {
    height: 5vmin;
    width: 10vmin;
    display: block;
    margin: auto;
    padding: 0;
}

.back-btn {
    top: 1.5vmin;
    left: 1.5vmin;
    font-size: 2vmin;
}

@media screen and (max-width: 500px) {
    .back-btn {
        top: 3vmin;
        left: 3vmin;
        font-size: 3vmin;
    }

    .btn {
        height: 10vmin;
        width: 16vmin;
        display: block;
        margin: auto;
        padding: 0;
    }
  }